<h1 id="oauth" tabindex="-1"><a class="header-anchor" href="#oauth" aria-hidden="true">#</a> OAuth</h1>
<blockquote>
<p>{warning} 此文档为企业微信内部应用开发的网页授权</p>
</blockquote>
<p><a href="https://work.weixin.qq.com/api/doc#90000/90135/91020" target="_blank" rel="noopener noreferrer">企业微信官方文档<OutboundLink/></a></p>
<p>创建实例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span> <span class="token comment">// 应用的 secret</span>
    <span class="token string single-quoted-string">'agent_id'</span> <span class="token operator">=></span> <span class="token number">100001</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="跳转授权" tabindex="-1"><a class="header-anchor" href="#跳转授权" aria-hidden="true">#</a> 跳转授权</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// $callbackUrl 为授权回调地址</span>
<span class="token variable">$callbackUrl</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'https://xxx.xxx'</span><span class="token punctuation">;</span> <span class="token comment">// 需设置可信域名</span>

<span class="token comment">// 返回一个 redirect 实例</span>
<span class="token variable">$redirect</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oauth</span><span class="token operator">-></span><span class="token function">redirect</span><span class="token punctuation">(</span><span class="token variable">$callbackUrl</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取企业微信跳转目标地址</span>
<span class="token variable">$targetUrl</span> <span class="token operator">=</span> <span class="token variable">$redirect</span><span class="token operator">-></span><span class="token function">getTargetUrl</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 直接跳转到企业微信授权</span>
<span class="token variable">$redirect</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h2 id="获取授权用户信息" tabindex="-1"><a class="header-anchor" href="#获取授权用户信息" aria-hidden="true">#</a> 获取授权用户信息</h2>
<p>在回调页面中，你可以使用以下方式获取授权者信息：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$user</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oauth</span><span class="token operator">-></span><span class="token function">detailed</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">user</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取用户信息</span>
<span class="token variable">$user</span><span class="token operator">-></span><span class="token function">getId</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 对应企业微信英文名（userid）</span>
<span class="token variable">$user</span><span class="token operator">-></span><span class="token function">getOriginal</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 获取企业微信接口返回的原始信息</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>获取用户其他信息需调用通讯录接口，参考：<a href="https://github.com/EasyWeChat/docs/blob/master/wework/contacts.md" target="_blank" rel="noopener noreferrer">企业微信通讯录API<OutboundLink/></a></p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code></code></pre><div class="line-numbers"></div></div>